import React, { useState, useEffect } from "react";

export default function Time({ className }) {
    const [time, setTime] = useState(new Date());

    const customDate = (d) => {
        const months = [
            "January",
            "February",
            "March",
            "April",
            "May",
            "June",
            "July",
            "August",
            "September",
            "October",
            "November",
            "December",
        ];
        const days = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
        const date = `${days[d.getDay()]},${
            months[d.getMonth()]
        } ${d.getDate()}`;
        var hours = d.getHours();
        const ampm = hours >= 12 ? "PM" : "AM";
        hours = hours % 12;
        hours = hours ? hours : 12;
        const t = `${hours}:${d.getMinutes()} ${ampm}`;
        return `${t} â€¢ ${date}`;
    };

    useEffect(() => {
        const timer = setInterval(() => {
            setTime(new Date());
        }, 1000);
        return () => {
            clearInterval(timer);
        };
    });
    return <p className={className}>{customDate(time)}</p>;
}
