import React, { useState } from "react";
import { NavLink } from "react-router-dom";
export default function Nav() {
    const [sidebar, setSidebar] = useState(false);
    const showSidebar = () => setSidebar(!sidebar);
    return (
        <nav className={sidebar ? "side-nav active" : "side-nav"}>
            <div className="side-nav-menu">
                <NavLink exact to="/" className="side-nav-logo">
                    PANGOLIN
                </NavLink>
                <ul className="list-group">
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/navigation"
                        className="list-group-item"
                    >
                        Header
                    </NavLink>
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/button"
                        className="list-group-item"
                    >
                        Button
                    </NavLink>
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/card"
                        className="list-group-item"
                    >
                        Card
                    </NavLink>
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/formcontrol"
                        className="list-group-item"
                    >
                        Form Control
                    </NavLink>
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/list"
                        className="list-group-item"
                    >
                        List
                    </NavLink>
                    <NavLink
                        exact
                        activeClassName="active"
                        to="/dropdowns"
                        className="list-group-item"
                    >
                        Drop Downs
                    </NavLink>
                </ul>
            </div>
            <button onClick={showSidebar} className="side-nav-toggle btn-icon">
                <ion-icon name="grid-outline"></ion-icon>
            </button>
        </nav>
    );
}
