import React from "react";
import Code from "../components/Code";
export default function home() {
    return (
        <>
            <div className="container py-5 text-center">
                <h1 className="home-header text-secondary">
                    <span className="text-primary">Pangolin</span>.css
                </h1>
                <h4>
                    A set of small, responsive CSS modules that you can use in
                    every web project.
                </h4>
            </div>
            <div className="container-fluid text-center">
                <Code
                    line={false}
                >{`<link rel="stylesheet" href="https://50um3n.github.io/pangolin-css/style/style.css">`}</Code>
            </div>
        </>
    );
}
